import React, { useEffect, useState } from 'react';
import './modal.css';
import { Link } from "gatsby";

let imageIndex = 0;
const Modal = (props) => {

  const [pickImage, setPickImage] = useState("");
  const [categoryName, setCategoryName] = useState("");

  const dataSources = props.dataSources;
  let maxNumber = props.maxNumber;

  const handleItem = (imageIndex) => {
    setPickImage(dataSources[imageIndex].image);
    setCategoryName(dataSources[imageIndex].category);
  }

  useEffect(() => {
    imageIndex = props.clickedIndex;
    handleItem(imageIndex);
  }, [])

  const handleArrow = (arrowNum) => {
    if(arrowNum === 1) {
      if(imageIndex === maxNumber) {
        imageIndex = 0;
        handleItem(imageIndex);
      }
      else {
        imageIndex += 1;
        handleItem(imageIndex);
      }
    }
    else if(arrowNum === 0){
      if(imageIndex === 0) {
        imageIndex = maxNumber;
        handleItem(imageIndex);
      }
      else {
        imageIndex -= 1;
        handleItem(imageIndex);
      }
    };
  };

  return (
    <div className="modal_container">
      <div className="white_box">
        <div className="left_box">
          <button 
            className="left_modal_button"
            onClick={() => handleArrow(0)}
          >
          <i className="fas fa-chevron-left fontFour"></i>
          </button>
        </div>
        <div className="close_box">
          <button 
            className="close_modal_button"
            onClick={props.closeModal}
          >&times;</button>
        </div>
        <div className="link_box"></div>
        <div className="title_box">
        <button className="link_category_button">
            <Link to={"/projects/#" + categoryName}>{categoryName}
            </Link>
          </button>
          <h2 className="modal_title fontTwo">
            Project Title
          </h2>
        </div>
        <div className="image_box">
          <img 
            src={pickImage}
            className= "modal_image"
            alt="images in modal"
          ></img>
        </div>
        <div className="comment_box">
          <h3 className="modal_comment fontTwo">
            I am the Goran san ! Bla bla bla bla bla~
          </h3>
        </div>
        <div className="right_box">
          <button 
            className="right_modal_button"
            onClick={() => handleArrow(1)}
          >
            <i className="fas fa-chevron-right fontFour"></i>
          </button>
        </div>
      </div>
    </div>
  )
}

export default Modal;